import { Component, OnInit } from '@angular/core';
import { BookingService } from '../booking.service';
import { AuthService } from '../auth.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import * as loggedin from '../app.component';



@Component({
  selector: 'app-booking',
  templateUrl: './booking.component.html',
  styleUrls: ['./booking.component.css']
})
export class BookingComponent implements OnInit {
bookings: any;
loggedin: any;
bookingForm: FormGroup;
  constructor(private booking : BookingService, private auth : AuthService, private fb : FormBuilder) { }

  ngOnInit() {
    this.booking.retrieveBookings(this.auth.getUserID()).subscribe(data => {
      this.bookings = data;
    })

    this.bookingForm = this.fb.group({
      fromdate: '',
      enddate: '',
      roomtype: '',
      bookingid: '',
      special: '',
      });
  }

  deleteBooking(bookingid : string) {
    console.log(bookingid);
    this.booking.deleteBooking(bookingid).subscribe();
  }

  updateBooking() {
    console.log(this.bookingForm.value.bookingid);
    this.booking.updateBooking(this.bookingForm.value.bookingid, this.bookingForm.value.fromdate, this.bookingForm.value.enddate, this.bookingForm.value.roomtype, this.bookingForm.value.special).subscribe();
  }
}
